import Vue from 'vue'

import Vuex from 'vuex'

import bonus from '@/common/es/api_bonus/index'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        token: '',
        user: { },
        user_unit: null,
        products: [ ],

        transfer: null,
        users_sponser: null,
        users_enroller: null
    },
    mutations: {
        changeAuth(state, token, user) {
            state.token = token
            state.user = user
            localStorage.setItem('oils_token', token)
            localStorage.setItem('oils_user', user)
        },
        clearToken(state) {
            state.token = ''
            state.user = { }
            localStorage.setItem('oils_token', '')
            localStorage.setItem('oils_user', {})
        },

        saveUnitUser(state, items) {
            // console.log('items =', items)
            state.user_unit = items
        },
        saveProducts(state, items) {
            state.products = items
        },
        
        // -------------------------------------------
        // 储存 用户
        saveUserSponser(state, the_sponser) {

            state.users_sponser = the_sponser
        },
        saveUserEnroller(state, the_enroller) {
            state.users_enroller = the_enroller
        },
        // 储存 翻译
        saveTransfer(state, the_transfer) {

            state.transfer = the_transfer
        }
    },
    getters: { 
        checkToken(state) {
            let res = false

            const token = localStorage.getItem('oils_token')
            if ((token == undefined) || (token == '') || (token == null)) {
                res = true
            } else {
                state.token = token
                state.user = localStorage.getItem('oils_user')
                
                console.log('token = ', token.substring(0, 15) + '...')
            }

            return v => {
              return res
            }
        },
    },
    modules: {

    }
})


