import util from '../util'

import conf from '../conf'
const NAMESPACE = conf.NAMESPACE
const CAN_USER_PV_LIMIT = conf.CAN_USER_PV_LIMIT

const clear = function(ul, index, ratios) {
    let res = []
    index += 1

    ul.map(e => {
        const i = { }
        let son_child = []
        if (util.judge_has_children(e)) {
            son_child = clear(e[
                NAMESPACE.children
            ], index, ratios)
        }

        i.name = e.name
        i[ NAMESPACE.ipv ] = e[ NAMESPACE.ipv ]
        i[ NAMESPACE.vip ] = e[ NAMESPACE.vip ]
        i[ NAMESPACE.index ] = e[ NAMESPACE.index ]
        
        if (son_child.length > 0) {
            i[ NAMESPACE.children ] = son_child
        }

        res.push(i)
        return i
    })
    return res
}
/*
        if (index >= 2) {
            let r = ratios[ index - 2 ]
            if (r == undefined) { r = 0.0 }
            i[ NAMESPACE.ratio ] = r
        }
*/

const _mul = function(x, y) {
    return util.floatMul(x, y)
}

const _add = function(x, y) {
    return util.floatAdd(x, y)
}

const bonus_compresser = function(ul, send_pv, send_layer, ratios) {
    
    let res = 0
    let r = ratios[send_layer]
    if (r == undefined) { r = 0 }

    ul.map(e => {
        let res_son = 0.0

        if (send_layer < conf.LAYER_DEPTH_NUM) {
            
            e[
                NAMESPACE.usd_layer
            ] = _mul(
                    _mul(r, e[ NAMESPACE.ipv ]), 
                    send_layer + 1
                )
    
            let pv = e[
                NAMESPACE.ipv
            ] + send_pv
    
            let layer = send_layer + 1
    
            if (util.judge_number(pv, conf.CAN_USER_PV_LIMIT)) {
                pv = 0
            } else {
                layer -= 1
            }
    
            if (util.judge_has_children(e)) {

                res_son = bonus_compresser(e[
                    NAMESPACE.children
                ], pv, layer, ratios)
            }

        } else {
            e[
                NAMESPACE.usd_layer
            ] = 0
        }

        res = _add(
                res, 
                _add(e[ NAMESPACE.usd_layer ], res_son)
            )
        
        return e
    })
    
    return res
}

const bonus_active_layer = function(users_sponser) {

    let res = 0
    
    return new Promise((rej, rev) => {
        const r = users_sponser.vip.ratio.filter(e => e != 0)

        users_sponser = clear([users_sponser], 0, r)[0]
        
        if (util.judge_has_children(users_sponser)) {

            res = bonus_compresser(users_sponser[
                NAMESPACE.children
            ], 0, 0, users_sponser[ NAMESPACE.vip ][ NAMESPACE.ratio ])

        }

        rej(res)
    })
}

export default bonus_active_layer



