<template>
    <div></div>
</template>

<script>
import rolling from '@/common/es/api_bonus/doing/_rolling_tree'
import computed from '@/common/es/api_bonus/doing/_computed'
import vip from '@/common/es/api_bonus/doing/_vip'

import recommend from '../../common/es/api_bonus/_bonus/_recomend'
import team_layer from '../../common/es/api_bonus/_bonus/_team_layer'
import active_house from '../../common/es/api_bonus/_bonus/_active_house'

    export default {
        props: {

        },
        name: '',
        data() {
            return {
                transfer: [ ],
                unit_users_sponser: [ ],
                unit_users_enroller: [ ],
            }
        },
        methods: {
            async buyLoading() {
                const res = await this.conn.get(
                    this.api.buys,
                    this.$store.state.token
                )

                return res.map(e => {
                        return {
                            num: e.num,
                            id: e.id,
                            pv: e.product.pv,
                            users_permissions_user: e.users_permissions_user.id,
                            buy_time: e.buy_time,
                            product: e.product.id,
                        }
                    })
            },
            async userLoading() {
                const res = await this.conn.get(
                    this.api.users,
                    this.$store.state.token
                )

                return res
            },
            rebuild_user(user) {
                delete user.created_at
                delete user.updated_at
                delete user.role
                delete user.provider
                delete user.confirmed
                delete user.blocked
                delete user.email
            },
            async init() {

                let res_sponser, res_enroller

                let users = await this.userLoading()
                let buys = await this.buyLoading()

                users.map(user => {
                    let res = []
                        buys.map(buy => { if (user.id == buy.users_permissions_user) { res.push(buy) } })
                        user.buys = res
                    return user
                })

                // 建立树状图
                res_sponser = await rolling(users, 'SPONSER', this.rebuild_user)
                res_enroller = await rolling(users, 'ENROLLER', this.rebuild_user)

                // 获取 中间数组， 获取 VIP 需要的参数
                this.transfer = await computed(res_sponser, 'SPONSER', this.transfer, null)
                this.transfer = await computed(res_enroller, 'ENROLLER', this.transfer, null)

                // 拿到 VIP
                this.transfer.map(e => {

                    e.vip = vip(e)
                    return e
                })

                this.unit_users_sponser = res_sponser
                this.unit_users_enroller = res_enroller

                // 计算 
                await recommend(res_enroller, this.transfer)
                await team_layer(res_sponser, this.transfer)
                await active_house(res_sponser, this.transfer)

                this.$store.commit('saveUserSponser', this.unit_users_sponser)
                this.$store.commit('saveUserEnroller', this.unit_users_enroller)
                this.$store.commit('saveTransfer', this.transfer)

            },
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


