
import conf from '../conf'
const NAMESPACE = conf.NAMESPACE

const ser_user = function(id, unid, sponser_id, enroller_id, buys, is_new, named, displayname) {
    const res = { id }

    res[
        NAMESPACE.unid
    ] = unid

    res[
        NAMESPACE.sponser_id
    ] = sponser_id

    res[
        NAMESPACE.enroller_id
    ] = enroller_id

    res[
        NAMESPACE.is_new
    ] = is_new

    res[
        NAMESPACE.named
    ] = named

    res[
        NAMESPACE.display_name
    ] = displayname

    res[
        NAMESPACE.buys
    ] = buys

    return res
}

const ser_buy = function(id, buy_time, num, product_id, pv, user_id) {
    let res = { id }

    res[
        NAMESPACE.buy_time
    ] = buy_time

    res[
        NAMESPACE.num
    ] = num

    res[
        NAMESPACE.product_id
    ] = product_id

    res[
        NAMESPACE.user_id
    ] = user_id

    res[
        NAMESPACE.pv
    ] = pv

    return res
}

const ser_bonus = function(usd_r, usd_ah, usd_tl) {
    let bouns = { }

    bouns[
        NAMESPACE.usd_recommend
    ] = usd_r

    bouns[
        NAMESPACE.usd_active_house
    ] = usd_ah

    bouns[
        NAMESPACE.usd_team_layer
    ] = usd_tl
}

export default {
    ser_buy,
    ser_user,
    ser_bonus
}


