
import util from '../util'
import conf from '../conf'

const NAMESPACE = conf.NAMESPACE

const CAN_USER_PV_LIMIT = conf.CAN_USER_PV_LIMIT
const ACTIVE_MEMBER_NUM = conf.ACTIVE_MEMBER_NUM
const ACTIVE_USD = conf.ACTIVE_USD

/*
    获取 PV 数量
*/
const getPv = function(user_item) {
    return util.numd_pv(user_item)
}

// 计算 奖金
const numd_usd = function(res, limit_member, usd) {
    const n = Math.floor(res.length / limit_member);
    return util.floatMul(usd, n) 
}

// 计算结果
const numd_result = function(user, member_num, limit_pv, name_field = NAMESPACE.children) {

    let pv = 0
    let res = []

    return new Promise((rej, rev) => {


        if (util.judge_has_children(user, name_field)) {
            
            const child_one = user[name_field]

            if (util.judge_number(child_one.length, member_num)) {

                child_one.map(e => {
                    if (util.judge_number(getPv(e), limit_pv)) {

                        if (util.judge_has_children(e, name_field)) {
                            const child_two = e[name_field]
                            const res_two = []

                            child_two.map(o => {

                                pv = getPv(o)
                                if (util.judge_number(pv, limit_pv)) {
                                    res_two.push(pv)
                                }

                            })

                            if (util.judge_number(res_two.length, member_num)) {
                                res.push(res_two)
                            }
                            
                        }

                    }
                })

            }
        }

        rej(res)
    })
}

// 调用
// user = Object
const numing_active_house_bonus = async function(user) {
    
    const res = await numd_result(user, ACTIVE_MEMBER_NUM, CAN_USER_PV_LIMIT)
    
    const u = numd_usd(res, ACTIVE_MEMBER_NUM, ACTIVE_USD)
    
    return u
}

export default numing_active_house_bonus


