import conf from './conf'

import util from './util'

import bonus_recommend from './bonusApi/bonus_recommend'
import bonus_active_house from './bonusApi/bonus_active_house'
import bonus_active_layer from './bonusApi/bonus_active_layer'

import ser from './tool/ser_items_key'

const NAMESPACE = conf.NAMESPACE

const numing = async function(user, call, code, depth) {

    let bonus = { }

    // 首购推荐
    if (code == null || code == 'usd_recommend') {
        let ur = await bonus_recommend(user) 

        if (depth) {
            ur = util.floatAdd(
                ur[2], 
                util.floatAdd(ur[0], ur[1])
            )
        }
        bonus[ NAMESPACE.usd_recommend ] = ur
    }

    // 活跃分店
    if (code == null || code == 'usd_active_house') {
        bonus[
            NAMESPACE.usd_active_house
        ] = await bonus_active_house(user) 
    }

    // 团队分层
    if (code == null || code == 'usd_team_layer') {
        bonus[
            NAMESPACE.usd_team_layer
        ] = await bonus_active_layer(user) 
    }

    user[ NAMESPACE.bonus ] = bonus

    // 执行回调
    call(user)

    // 递归
    if (util.judge_has_children(user)) {
        
        user[
            NAMESPACE.children
        ].map(e => {

            numing(e, call, code, depth)

        })
    }
}

// 一套递归，运算所有，并且有一个回调用于

// user = Object = 0 代
// call = Function = 计算完成奖励之后的回调函数，会因为递归而传入用户树形图中的每一位 User
// code = String = 指定仅计算某个奖励
// depth = Boolean = 是否深度地输出结果
const bonus = function(user, call, code = null, depth = true) {
    console.time("Bonus");
    numing(user, call, code, depth)
    console.timeEnd("Bonus");
}

export default {
    bonus,
    ...ser
}


