
const Account = () => import('@/pages/Account/Account')
const AccountResult = () => import('@/pages/Account/Result/AccountResult')

const AccountTest = () => import('@/pages/Account/Test/AccountTest')

export default {
    
    path: '/home/account',
    component: Account,
    
    redirect:'/home/account/result',
    children: [
        {
            path: '/home/account/result',
            component: AccountResult,
        },
        {
            path: '/home/account/test',
            component: AccountTest
        }
    ]
}


