<template>
    <div></div>
</template>

<script>
import api_bonus from '../../common/es/bonus/bonus'

import bonus from '@/common/es/api_bonus/index'

    export default {
        name: '',
        data() {
            return {
                transfer: [ ],
                unit_users_sponser: [ ],
                unit_users_enroller: [ ],
            }
        },
        mounted() {
        },
        methods: {
            async init() {
                let user_unit = await this.loadingItems()

                this.$store.commit('saveUnitUser', user_unit)

                const products = await this.productsLoading()
                this.$store.commit('saveProducts', products)

                console.log('Loading Completed!')
            },

            async loadingItems() {
                let items = [ ]

                const users = await this.loadingUser()
                const buys = await this.loadingBuys()

                if (users) {
                    users.map(e => { 

                        const _buy = buys.filter(buy => {
                            if (buy.user == e.id) {
                                return true
                            }
                            return false
                        })

                        items.push(
                            api_bonus.ser_user(
                                e.id,
                                e.unid,
                                e.sponser_id,
                                e.enroller_id,
                                _buy,
                                e.is_new,
                                e.username,
                                e.displayname
                            )
                        )
                    })
                }

                return items
            },

            async productsLoading() {
                let prod = await this.conn.get(this.api.products, this.$store.state.token)

                if (prod) {
                    prod = prod.map(e => {
                        return {
                            id: e.id,
                            named: e.named,
                            price: e.price,
                            pv: e.pv
                        }
                    })
                }
                
                return prod
            },

            async loadingUser() {

                console.log('搜集用户资料 token =', this.$store.state.token)
                const res = await this.conn.get(this.api.users, this.$store.state.token)

                if (res) {
                    
                }

                return res
            },
            async loadingBuys() {
                let res = await this.conn.get(this.api.buys, this.$store.state.token)

                if (res) {
                    res = res.map(e => {
                        return api_bonus.ser_buy(e.id, e.buy_time, e.num, e.product.id, e.product.pv, e.users_permissions_user.id)
                    })
                }
                
                return res
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


